<template>
  <div>
    <h1>Google Sheets Data</h1>
    <div v-if="loading">Loading...</div>
    <div v-if="error">{{ error }}</div>
    <table v-if="sheetsData.length">
      <tr v-for="(row, rowIndex) in sheetsData" :key="rowIndex">
        <td v-for="(cell, cellIndex) in row" :key="cellIndex">{{ cell }}</td>
      </tr>
    </table>

    <h3>Add New Entry</h3>
    <div>
      <label for="id">ID:</label>
      <input id="id" v-model="newEntry.id" placeholder="Enter ID" />
    </div>
    <div>
      <label for="name">Name:</label>
      <input id="name" v-model="newEntry.name" placeholder="Enter Name" />
    </div>
    <div>
      <label for="password">Password:</label>
      <input id="password" v-model="newEntry.password" placeholder="Enter Password" />
    </div>
    <button @click="addRow">Add New Row</button>
    <div v-if="needsLogin">
      <p>你还没有登录，请点击下方按钮进行授权登录。</p>
      <button @click="redirectToGoogleLogin">登录 Google</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sheetsData: [],
      loading: false,
      error: null,
      accessToken: null,
      newEntry: {
        id: '',
        name: '',
        password: ''
      },
      needsLogin: false,
      refreshInterval: null // 用于存储定时器的引用
    };
  },
  mounted() {
    this.handleRedirect();
    this.startAutoRefresh();
  },
  beforeUnmount() {
    if (this.refreshInterval) {
      clearInterval(this.refreshInterval);
    }
  },
  methods: {
    handleRedirect() {
      const urlParams = new URLSearchParams(window.location.search);
      const code = urlParams.get('code');

      if (code) {
        this.fetchAccessTokenByCode(code);
      } else {
        this.initClient();
      }
    },

    startAutoRefresh() {
      // 设置每5分钟强制刷新一次access_token
      this.refreshInterval = setInterval(() => {
        this.forceRefreshAccessToken();
      }, 5 * 60 * 1000); // 5分钟
    },

    async forceRefreshAccessToken() {
      const refreshToken = localStorage.getItem('refresh_token');
      if (refreshToken) {
        console.log('每5分钟强制刷新一次access_token...');
        await this.fetchAccessToken(refreshToken);
      } else {
        console.log('没有可用的refresh_token，无法刷新access_token');
      }
    },

    async fetchAccessTokenByCode(code) {
      try {
        const response = await fetch('https://oauth2.googleapis.com/token', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
          },
          body: new URLSearchParams({
            client_id: process.env.VUE_APP_GOOGLE_CLIENT_ID,
            client_secret: process.env.VUE_APP_GOOGLE_CLIENT_SECRET,
            code: code,
            redirect_uri: process.env.VUE_APP_REDIRECT_URI,
            grant_type: 'authorization_code'
          })
        });

        const data = await response.json();
        console.log('获取 access_token 和 refresh_token 的响应:', data);

        if (data.access_token) {
          this.accessToken = data.access_token;
          const now = Date.now();
          localStorage.setItem('access_token', data.access_token);
          localStorage.setItem('access_token_time', now);
          if (data.refresh_token) {
            localStorage.setItem('refresh_token', data.refresh_token);
          }
          this.fetchGoogleSheetsData();
        } else {
          this.error = 'Failed to retrieve access token.';
        }
      } catch (error) {
        this.error = 'Error fetching access token: ' + error.message;
      }
    },

    initClient() {
      const refreshToken = localStorage.getItem('refresh_token');
      //const tokenTime = localStorage.getItem('access_token_time');
      console.log('检查是否有 refresh_token:', refreshToken);

      if (!refreshToken) {
        console.log('没有找到 refresh_token，提示用户需要登录');
        this.needsLogin = true;
      } else {
        this.accessToken = localStorage.getItem('access_token');
        this.fetchGoogleSheetsData();
      }
    },

    async fetchAccessToken(refreshToken) {
      try {
        console.log('正在使用 refresh_token 强制刷新 access_token...');
        const response = await fetch('https://oauth2.googleapis.com/token', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
          },
          body: new URLSearchParams({
            client_id: process.env.VUE_APP_GOOGLE_CLIENT_ID,
            client_secret: process.env.VUE_APP_GOOGLE_CLIENT_SECRET,
            refresh_token: refreshToken,
            grant_type: 'refresh_token'
          })
        });

        const data = await response.json();
        console.log('刷新 access_token 的响应:', data);

        if (data.access_token) {
          this.accessToken = data.access_token;
          const now = Date.now();
          localStorage.setItem('access_token', data.access_token);
          localStorage.setItem('access_token_time', now);
          console.log('access_token 已更新:', data.access_token);
        } else {
          this.error = 'Failed to retrieve access token.';
          console.log('无法获取新的 access_token:', data);
        }
      } catch (error) {
        this.error = 'Error fetching access token: ' + error.message;
        console.log('刷新 access_token 时发生错误:', error);
      }
    },

    async fetchGoogleSheetsData() {
      if (!this.accessToken) {
        this.error = 'Access token is required';
        return;
      }

      this.loading = true;
      const SHEET_ID = process.env.VUE_APP_GOOGLE_SHEET_ID;
      const RANGE = process.env.VUE_APP_GOOGLE_SHEET_RANGE;

      fetch(`https://sheets.googleapis.com/v4/spreadsheets/${SHEET_ID}/values/${RANGE}?access_token=${this.accessToken}`)
        .then(response => response.json())
        .then(data => {
          this.sheetsData = data.values || [];
          this.loading = false;
          console.log('成功获取 Google Sheets 数据:', this.sheetsData);
        })
        .catch(error => {
          this.error = 'Error fetching data: ' + error.message;
          this.loading = false;
          console.log('获取 Google Sheets 数据时发生错误:', error);
        });
    },

    async addRow() {
      const now = Date.now();
      localStorage.setItem('last_operation_time', now);
      console.log('记录当前操作时间:', new Date(now).toLocaleString());

      if (!this.accessToken) {
        this.error = 'Access token is required';
        return;
      }

      if (!this.newEntry.id || !this.newEntry.name || !this.newEntry.password) {
        this.error = 'All fields are required';
        return;
      }

      const SHEET_ID = process.env.VUE_APP_GOOGLE_SHEET_ID;
      const RANGE = 'Sheet1';

      const valueRangeBody = {
        values: [
          [this.newEntry.id, this.newEntry.name, this.newEntry.password]
        ],
      };

      fetch(`https://sheets.googleapis.com/v4/spreadsheets/${SHEET_ID}/values/${RANGE}:append?valueInputOption=RAW&insertDataOption=INSERT_ROWS`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.accessToken}`
        },
        body: JSON.stringify(valueRangeBody)
      })
      .then(response => response.json())
      .then(data => {
        this.fetchGoogleSheetsData(); // 添加行后刷新数据
        this.newEntry.id = '';
        this.newEntry.name = '';
        this.newEntry.password = '';
        console.log('成功添加新行:', data);
      })
      .catch((error) => {
        this.error = 'Error adding row: ' + error.message;
        console.log('添加新行时发生错误:', error);
      });
    },

    redirectToGoogleLogin() {
      const clientId = process.env.VUE_APP_GOOGLE_CLIENT_ID;
      const redirectUri = process.env.VUE_APP_REDIRECT_URI;
      const scope = 'https://www.googleapis.com/auth/spreadsheets';
      const authUrl = `https://accounts.google.com/o/oauth2/v2/auth?client_id=${clientId}&redirect_uri=${redirectUri}&response_type=code&scope=${scope}&access_type=offline&prompt=consent`;

      console.log('重定向到:', authUrl);

      window.location.href = authUrl;
    }
  }
}
</script>

